//Fonts
$font-family-primery: "Alumni Sans", "Arial", sans-serif;
$font-family-secondary: "Roboto Condensed", "Arial", sans-serif;
$font-weight-light: 300;
$font-weight-regular: 400;
$font-weight-medium: 500;
$font-weight-semibold: 600;
$font-weight-bold: 700;

// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-sapphire-blue:               #102654;
$color-egyptian-blue:               #103aae;
$color-ghost-white:                 #f8f9fc;
$color-torch-blackred:              #ed0233;
$color-russian-black:               #030a1a;
$color-torch-red:                   #ff121f;
$color-crimson-red:                 #d10a42;
$color-link-water-grey:             #c8c9cc;
$color-spindle-grey:                #b6bdcc;
$color-pale-cornflower-blue:        #afd3fb;
$color-resolution-blue:             #1c3374;
$color-light-slate-grey:            #74819c;
$color-persian-blue:                #2e35ac;

// Gradient
$red-to-blue:                       linear-gradient(151deg, #ed0233 0%, #003eb7 100%);
$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);
$crimson-to-smalt:                  linear-gradient(160deg, #d10a42, #00359f);
$conic-blue-to-red:                 conic-gradient(from 161deg at 16.03% 39.06%, #003d85 32.24deg, #b80027 290.31deg, hsl(212, 100%, 26%));
$conic-blue-to-red-mobile:          conic-gradient(from 191deg at 36.03% 34.06%, #003d85 32.24deg, #b80027 290.31deg, hsl(212, 100%, 26%));
// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default:                        16px;

// у некоторых шрифтов в ios возникают проблемы с символом рубля
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble:                         "Rouble", "Arial", sans-serif;

$ff-placeholder:                    "Placeholder", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-768:                            768px;
$vp-1024:                           1024px;
$vp-1200:                           1200px;
$vp-1280:                           1280px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
